.Loader{--backgroundColor:var(
      --loaderBackgroundColor,var(--color-control-bg-primary)
    );height:calc(100% - var(--loader-size));left:50%;position:relative;top:50%;transform:translate(-50%,-50%);width:100%}.Loader:before{-webkit-animation:loader 1s ease infinite;animation:loader 1s ease infinite;left:calc(50% - var(--loader-size)*2.5)}.Loader:after,.Loader:before{background:var(--backgroundColor);border-radius:50%;content:"";height:var(--loader-size);position:absolute;top:calc(50% - var(--loader-size)*.5);width:var(--loader-size)}.Loader:after{-webkit-animation:loader 1s ease -.36s infinite;animation:loader 1s ease -.36s infinite;left:calc(50% + var(--loader-size)*1.5)}.Loader-Dot{-webkit-animation:loader 1s ease -.18s infinite;animation:loader 1s ease -.18s infinite;background:var(--backgroundColor);border-radius:50%;height:var(--loader-size);left:calc(50% - var(--loader-size)*.5);position:absolute;top:calc(50% - var(--loader-size)*.5);width:var(--loader-size)}.Loader_size_s{--loader-size:4px}.Loader_size_m{--loader-size:8px}@-webkit-keyframes loader{50%{transform:scale(1.5)}}@keyframes loader{50%{transform:scale(1.5)}}